//
// accordion.scss
//

.accordion {
    .accordion-button {
        font-weight:  400;
    }

    .accordion-body {
        color: gray;
    }

    &.accordion-icon-none {
        .accordion-button {
            &::after {
                content: "";
                background-image: none !important;
            }

            &:not(.collapsed) {
                &::after {
                    content: "";
                }
            }
        }
    }
}

// Accordion with icon

.custom-accordionwithicon {
    .accordion-button {
        &::after {
            // background-image: none !important;
            font-family: "Material Design Icons";
            // content: "\F0142";
            font-size: 1.1rem;
            vertical-align: middle;
            line-height: 0.8;
        }

        &:not(.collapsed) {
            &::after {
                // background-image: none !important;
                // content: "\F0140";
                margin-right: -3px;
            }
        }
    }
}

