// border accordion

.accordion-border-box {
    .accordion-item {
        border-top: 1 solid gray;
        border-radius: 5px;

        &:not(:first-of-type) {
            margin-top: 8px;
        }

        .accordion-button {
            border-radius: 5px;

            &:not(.collapsed) {
                border-bottom-left-radius: 0;
                border-bottom-right-radius: 0;
            }
        }
    }
}

.custom-accordion-border {
    .accordion-item {
        border-left: 3px solid gainsboro;
    }
}

