// landing page
.process-card {
    .process-arrow-img {
        left: auto;
        right: 75%;
        transform: scaleX(-1);
    }
}
.icon-effect {
    &::before {
        left: auto;
        right: 0;
    }
}

.marketplace-swiper {
  .swiper-button-next, .swiper-button-prev {
    top: 22px;
  }
}

.alert-msg-show {
